@keyframes FadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes zoomOut {
  0% {
    transform: translate(-50%, -50%) scale(1.9); /* Adjust the scale as needed */
  }
  100% {
    transform: translate(-50%, -50%) scale(1.4);
  }
}


@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}